import {mapState, mapActions} from 'vuex'
import helper from '@/mixins/helper'
import tool from '@/utils/tool'
import Tab from "@/components/Tab"
import Loading from "@/components/Loading"

export default {
    name: "Order",
    mixins: [helper],
    components: { Tab, Loading },
    data() {
        return {
            topNav:[
                {
                    label: "自购订单"
                },
                {
                    label: "粉丝订单"
                }
            ],
            navList: [
                {
                    label: "全部",
                },
                {
                    label: "已付款",
                },
                {
                    label: "已收货",
                },
                {
                    label: "退款"
                }

            ],
            currentTab:0,
            current: 0,
            loadMore: true,
            items: [1,2,3,4,5],

            fansCurrent: 0,
            loading: true,
            page: 1,
            fansPage: 1,
            orderList: [1,2,3,4,5],
        }
    },
    methods: {
        changeTab(item, index){
            this.currentTab = index
            if (index == 0) {
                this.getOrderList()
            } else {
                this.getFansOrder()
            }
        },
        listenTabChange(data){
            console.log(`listenTabChange data : `, data)
            this.current = data.index
            this.page = 1
            this.getOrderList()
        },
        switchTab(data) {
            console.log(`switchTab data : `, data)
            this.switch(data)
        },
        switch(data) {
            this.fansCurrent = data.index
            this.fansPage = 1
            this.$refs.listBox.scrollTop = 0;
            this.getFansOrder()
        },
        getOrderList() {
            console.log('getOrderList')
        },
        getFansOrder() {
            console.log('getFansOrder')
        },
    },
}



