export default {
    name: "GoodsItem",
    props: {
        List: {
            type: Array,
            default: function () {
                return []
            },
        },
        CurrentTab: {
            type: [String, Number],
            default: 0,
        },
        Position: { //tab栏位置
            type: String,
            default: ""
        },
        Width: {
            type: String,
            default: "100%"
        },
        Height: {
            type: String,
            default: "2"
        },
        BgColor: {
            type: String,
            default: "#FFFFFF"
        },
        BorderTopColor: {
            type: String,
            default: "#EEEEEE"
        },
        BorderBottomColor: {
            type: String,
            default: "#EEEEEE"
        },
        Color: {
            type: String,
            default: "#333333"
        },
        BarColor: {
            type: String,
            default: "#ED3C26"
        },
        Top: {
            type: [String, Number],
            default: 0
        }
    },
    data() {
        return {
            Left: 0,
            Right: 0,
        }
    },
    mounted() {
        this.init()
    },
    watch: {
        List(newVal) {
            let len = newVal.length;
            let curIndex = this.CurrentTab;
            this.Left = curIndex / len
            this.Right = (len - 1 - curIndex) / len
        },
        CurrentTab(newVal) {
            let len = this.List.length;
            let curIndex = newVal;
            this.Left = curIndex / len
            this.Right = (len - 1 - curIndex) / len
        },
    },
    methods: {
        init() {
            let len = this.List.length;
            let curIndex = this.CurrentTab;
            this.Left = curIndex / len
            this.Right = (len - 1 - curIndex) / len
        },
        _SwitchTabInner(item, index) {
            let len = this.List.length;
            let curIndex = index
            this.Left = curIndex / len
            this.Right = (len - 1 - curIndex) / len

            this.$emit('tabchange', {index})
        },
    }
}



